.opinionsPage {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  h1 {
    margin-top: 50px;
    text-transform: uppercase;
    font-size: 36px;
    letter-spacing: 2px;
  }
  .opinionsBox {
    display: flex;
    flex-direction: row;
    width: 90%;
    min-height: 100%;
    padding: 20px;
    .opinion {
      display: flex;
      flex-direction: column;
      border: 6px solid orange;
      border-radius: 40px;
      padding: 20px;
      margin: 5px;
      h2 {
        border-top: 4px solid orange;
        border-bottom: 3px orange solid;
        width: 100%;
        text-align: center;
        padding-left: 20px;
        padding-bottom: 10px;
        padding-top: 10px;
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
        color: rgba(0, 0, 0, 0.7);
        letter-spacing: 1px;
      }
      p {
        padding: 10px;
        color: rgba(0, 0, 0, 0.55);
        border-bottom: 3px orange solid;
      }
      .checked {
        color: orange;
      }
      .stars {
        padding: 10px 0;
        border-bottom: 3px orange solid;
        width: 100%;
        text-align: center;
        span {
          font-size: 1.2em;
          margin: 0 2px;
        }
      }
      .projectLink {
        padding: 10px 0;
        border-bottom: 4px orange solid;
        width: 100%;
        text-align: center;
        border-bottom-left-radius: 8px;
        border-bottom-right-radius: 8px;
        &:hover {
          cursor: pointer;
        }
      }
    }
  }
}

@media (max-width: 700px) {
  .opinionsPage {
    width: 100vw;
    align-items: center;
    padding-top: 20px;
    box-shadow: 0px 19px 22px -2px rgba(184, 184, 204, 1);
    .opinionsBox {
      margin-top: 10%;
      overflow-x: scroll;
      .opinion:first-child {
        margin-left: 10%;
      }
      .opinion:last-child {
        margin-right: 10%;
      }
      .opinion {
        min-width: 80%;
        margin-top: 10%;
      }
    }
  }
}

@media (min-width: 700px) {
  .opinionsPage {
    align-items: center;
    .opinionsBox {
      flex-wrap: wrap;
      margin-top: 5%;
      justify-content: center;
      .opinion {
        width: 250px;
        justify-content: center;
        margin-bottom: 5%;
        text-align: center;
      }
    }
  }
}
